<style scoped>
  .layout{
    border: 1px solid #d7dde4;
    background: #f5f7f9;
  }
  .layout-logo{
    width: 100px;
    height: 30px;
    border-radius: 3px;
    float: left;
    left: 20px;
    float:left;
    position: absolute;
    top: 7px;
  }
  .logoImg{
    height: 70px;
    width: 70px;
  }

  .layout-title{
    position: absolute;
    top: 15px;
    left:100px;
    /*line-height: 60px;*/
    color:white;
    font-family: "Microsoft Himalaya";
    font-size: 20px;
    cursor: default;
  }
  .layout-nav{
    width: 420px;
    margin: 0 auto;
  }
  .layout-assistant{
    width: 300px;
    margin: 0 auto;
    height: inherit;
  }
  .layout-breadcrumb{
    padding: 10px 15px 0;
  }
  .layout-content{
    min-height: 200px;
    width: 70%;
    margin: 15px auto;
    overflow: hidden;
    background: #fff;
    border-radius: 4px;
  }
  .layout-content-main{
    padding: 10px;
  }
  .layout-copy{
    text-align: center;
    color: #9ea7b4;
    padding: 20px;
    position: fixed;
    bottom: 0;
    width: 100%;
    background: #495060;
    height: 60px;
  }

  .headerHeight{
    height:80px;
    padding-top: 10px;
    padding-bottom: 10px;
    position: fixed;
    top: 0;
    width: 100%;
  }


</style>
<template>
  <div class="layout">
    <Menu mode="horizontal" theme="dark" active-name="1" class="headerHeight">
      <div class="layout-logo"><img class="logoImg" src="../assets/cqIcon.png"/></div>
      <div class="layout-title">海洋灵玉</div>
      <div class="layout-nav" >
        <MenuItem name="1">
          <Icon type="home"></Icon>
          主页
        </MenuItem>
        <MenuItem name="2">
          <Icon type="heart"></Icon>
          结缘堂
        </MenuItem>
        <MenuItem name="3">
          <Icon type="videocamera"></Icon>
          加工影像
        </MenuItem>

      </div>
    </Menu>
    <div class="layout-content">
      <div class="layout-content-main">
        <keep-alive>
          <router-view></router-view>
        </keep-alive>

      </div>

    </div>


    <div class="layout-copy">
      <MenuItem name="2" class="footerItem">
        <Icon type="email"></Icon>
        微信号 : boboanyko
      </MenuItem>
    </div>
    <rightqode></rightqode>
  </div>
</template>
<script>
  import rightqode from '../pages/rightQCode'

  export default {
    components:{rightqode},
    data() {
      return {
        msg: 'aaa'
      }
    }
  }
</script>



