import Vue from 'vue'
import Layout from './components/layout'
import CqContent from  './pages/cqcontent'
import iView from 'iview'
import 'iview/dist/styles/iview.css'
import  VueRouter from 'vue-router'
import RightQCode from './pages/rightQCode'

Vue.use(iView)
Vue.use(VueRouter)

let router = new VueRouter({
  mode:'history',
  routes:[
    {
      path:'/',
      component:CqContent
    },
    {
      path:'/',
      component:RightQCode
    }
  ]
})




new Vue({
  el: '#app',
  render: h => h(Layout),
  router,
  template:'<Layout/>',
  components:{ Layout}
})



